<template>
    <div class="therapist-home">
        <my-search ref="mysearch" :tableclass="'shop-width'" class="my-search" @my-search-focus="onScearchFocus">
        </my-search>
        <keep-alive>
            <router-view @search-panel-leave="onSearchPanelLeave"></router-view>
        </keep-alive>
    </div>
</template>

<script>
import mySearch from "../../../public/mySearch.vue";
import { mapState } from "vuex";
import Vue from "vue";
import { Toast } from "mint-ui";
import "mint-ui/lib/toast/style.css";
Vue.component(Toast.name, Toast);
import bus from '../../../../common/js/eventBus.js';
import {MessageBox} from "../../../popupBox/messageBox/message-box.js"
Vue.component(MessageBox.name, MessageBox);

export default {
  name: "therapist-home",
  data() {
    return {
        cellData:[],
        bindItem:null,
        cancelBindItem:null
    };
  },
  components: {
    mySearch
  },
  created() {
  },
  beforeRouteLeave(to, from, next){  
    MessageBox.close();
    // bus.$off("therapis-bind-call",this.onTherapisBindCall);
    // bus.$off("therapis-bind-cancel-call",this.onTherapisBindCancelCall);
    next();
  },
  mounted(){
    bus.$on("therapis-bind-call",this.onTherapisBindCall);
    bus.$on("therapis-bind-cancel-call",this.onTherapisBindCancelCall);
  },
  computed: {
    ...mapState(["openid", "path","userid","therapistList"])
  },
  methods: {
    onSearchPanelLeave(){
      this.$refs.mysearch.initiativeBlur();
    },
    onScearchFocus(){
      if(this.$route.name!="therapistList") return;
      this.$router.push({name:"therapistSearch"});
    },
    onTherapisBindCall(cellData){
      this.bindItem=cellData;
      var spanLeft1='<span style="font-weight:700;color:#42495C;">';
      var spanLeft2='<span style="font-weight:700;color:#D2B48C;">';
      var spanRight='</span>';  
      MessageBox.confirm('绑定确认',{title: '绑定确认',
        message: "确定将"+spanLeft1+" \'"+this.bindItem.username+"\' "+spanRight+"与"+spanLeft2+" \'"+'您'+"\' "+spanRight+"绑定?",
        showCancelButton: true}).
        then(action => {
          this.requestBindRelationship();
        }).catch(response=>{
          //请求出错处理
          console.log(response);
        });
    },
    requestBindRelationship(){       //请求绑定关系
      this.$store.dispatch("toggleLoading", true);
      this.$http
      .post(this.path + "php/Route.php?c=Store", {
        type:'AddBeauties',
        bid:this.userid,
        uid:this.bindItem.beautiesid
      })
      .then(response => {
        let resultData = response.body;
        if(resultData.error=="ok"){
          Toast({message: "绑定成功",iconClass: "iconfont icon-tishi1",duration: 1500});
          this.bindItem.bindMe=true;
          let therapistList=this.therapistList;
          therapistList.unshift(this.bindItem);
          this.$store.commit('UPDATA_THERAPIST_LIST',therapistList);
        }else{
          Toast({message: "绑定失败",iconClass: "iconfont icon-tishi1",duration: 1500});
        }
        this.$store.dispatch("toggleLoading", false);
      })
      .catch(response=>{
        //请求出错处理
        console.log(response);
        this.$store.dispatch("toggleLoading", false);
      });
    },
    onTherapisBindCancelCall(cellData){
      this.cancelBindItem=cellData;
      var spanLeft1='<span style="font-weight:700;color:#42495C;">';
      var spanLeft2='<span style="font-weight:700;color:#D2B48C;">';
      var spanRight='</span>';  
      MessageBox.confirm('取消绑定确认',{title: '取消绑定确认',
        message: "确定取消"+spanLeft1+" \'"+this.cancelBindItem.username+"\' "+spanRight+"与"+spanLeft2+" \'"+'您'+"\' "+spanRight+"的绑定关系?",
        showCancelButton: true}).
        then(action => {
          this.requestBindCancel();
        }).catch(response=>{
          //请求出错处理
          console.log(response);
        });
    },
    requestBindCancel(){
      this.$store.dispatch("toggleLoading", true);
      this.$http
      .post(this.path + "php/Route.php?c=Store", {
        type:'delBeauties',
        bid:this.userid,
        uid:this.cancelBindItem.beautiesid
      })
      .then(response => {
        this.$store.dispatch("toggleLoading", false);
        let resultData = response.body;
        if(resultData.error=="ok"){
          Toast({message: "解绑成功",iconClass: "iconfont icon-tishi1",duration: 1500});
          let therapistList=this.therapistList;
          let index=therapistList.indexOf(this.cancelBindItem);
          if(index<0) return;
          therapistList.splice(index,1);
          this.$store.commit('UPDATA_THERAPIST_LIST',therapistList);
        }else{
          Toast({message: "解绑失败",iconClass: "iconfont icon-tishi1",duration: 1500});
        }
      })
      .catch(response=>{
        //请求出错处理
        this.$store.dispatch("toggleLoading", false);
      });
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
.therapist-home {
  height 100%; 
  box-sizing: border-box;
  display flex;
  flex-direction column;
  flex 1;

  .my-search {
    position relative;
    z-index 1;
    box-sizing border-box;
    width: 100%;
    background-color #e5e5e5;
    box-shadow: 0 1px 1px rgba(80, 80, 80, 0.25);
    .icon-chakan {
      color: #42495C;
      font-size: 20px;
      font-weight 700;
    }
  }
}

</style>



